.navbar {
    /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%); */
    background-color: rgb(26, 23, 23);
    height: 80px;
    width: 100%;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
  }

  .navbar-container {
    display: flex;
    /* justify-items: flex-end; */
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 100%;
    /* max-width: 1500px; */
    font-family: 'Gruppo', cursive;
  }

  
  .navbar-logo {
    font-family: 'Special Elite', cursive;
    /* font-family: 'Special Elite', cursive; */
    color: #fff;
    width: 100%;
    padding-top: 15px;
    padding-left: 100px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    /* color: blue; */
    display: grid;
    grid-template-columns: repeat(5, auto);
    /* grid-gap: 0px; */
    list-style: none;
    text-align: center;
    width: 100%;
  }
  
  .nav-item {
    height: 80px;
    font-size: 19px;
  }
  
  .nav-links {
    height: 100%;
    color: #fff;
    text-decoration: none;
  }

  .nav-item {
    display: flex;
    align-items: center;
    padding: 2.3rem 1rem 0 1rem;
      
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars, .fa-times {
    color: #fff !important;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    
    .navbar-container {
      justify-items: flex-end;
      justify-content: left;
      padding: 0 20px;
    }

    .menu-icon {
      padding: 6px 0 0 0;
    }
    
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      padding-left: 0px;
      text-align: center;
      width: fit-content;
      /* position: absolute; */
      /* top: 0; */
      /* left: 0; */
      /* transform: translate(25%, 50%); */
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }